// Login.js
import React, { useState } from 'react';
import supabase from './supabaseClient'; //  Supabase 

function Login() {
  const [account, setAccount] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');
  const [currentUser, setCurrentUser] = useState(null); // 
  const [newName, setNewName] = useState('');
  const [newAccount, setNewAccount] = useState('');
  const [newPassword, setNewPassword] = useState('');

  // 
  const handleLogin = async (e) => {
    e.preventDefault();

    // 
    const { data, error } = await supabase
      .from('users')
      .select('*')
      .eq('account', account)
      .eq('password', password)
      .single();  //  .single() 

    if (error) {
      setMessage('');
    } else if (data) {
      setCurrentUser(data); // 
      setNewName(data.name);
      setNewAccount(data.account);
      setMessage(` ${data.name}`);
    } else {
      setMessage('');
    }
  };

  // 
  const handleUpdate = async (e) => {
    e.preventDefault();

    // 
    if (!newName || !newAccount || !newPassword) {
      setMessage('');
      return;
    }

    // 
    const { data, error } = await supabase
      .from('users')
      .update({ name: newName, account: newAccount, password: newPassword })
      .eq('id', currentUser.id);

    if (error) {
      setMessage(`: ${error.message}`);
    } else {
      // 
      setCurrentUser({ ...currentUser, name: newName, account: newAccount });
      setMessage('');
    }
  };

  // 
  const handleDeleteAccount = async () => {
    const { error } = await supabase
      .from('users')
      .delete()
      .eq('id', currentUser.id);

    if (error) {
      setMessage(`: ${error.message}`);
    } else {
      setMessage('');
      setCurrentUser(null); // 
    }
  };

  return (
    <div className="container mt-4">
      <h2></h2>
      {!currentUser ? (
        <form onSubmit={handleLogin}>
          <div className="form-group">
            <label>Email</label>
            <input
              type="text"
              className="form-control"
              value={account}
              onChange={(e) => setAccount(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label></label>
            <input
              type="password"
              className="form-control"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <button type="submit" className="btn btn-primary"></button>
        </form>
      ) : (
        // 
        <div>
          <h3>{currentUser.name}</h3>
          <h4></h4>
          <form onSubmit={handleUpdate}>
            <div className="form-group">
              <label></label>
              <input
                type="text"
                className="form-control"
                value={newName}
                onChange={(e) => setNewName(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label>Email</label>
              <input
                type="text"
                className="form-control"
                value={newAccount}
                onChange={(e) => setNewAccount(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label></label>
              <input
                type="password"
                className="form-control"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                required
              />
            </div>
            <button type="submit" className="btn btn-primary"></button>
          </form>

          <button
            className="btn btn-danger mt-2"
            onClick={handleDeleteAccount} // 
          >
            
          </button>

          <button
            className="btn btn-secondary mt-2 ml-2"
            onClick={() => setCurrentUser(null)} // 
          >
            
          </button>
        </div>
      )}

      {message && <p className="mt-3">{message}</p>}
    </div>
  );
}

export default Login;
