import { computeHeadingLevel } from '@testing-library/dom';
import React, { useState } from 'react';
import './App.css';

function App() {
  //  users  React state
  const [users, setUsers] = useState([
    { Name: 'John Doe', account: 'john_doe', password: 'password123' },
    { Name: 'Jane Smith', account: 'jane_smith', password: 'password456' },
    { Name: 'Admin', account: 'admin', password: 'admin123' }
  ]);

  const [account, setAccount] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');
  const [currentUser, setCurrentUser] = useState(null); // 

  // 
  const handleLogin = (e) => {
    e.preventDefault();

    // 
    const user = users.find((user) => user.account === account && user.password === password);

    if (user) {
      setMessage(` ${user.Name}`);
      setCurrentUser(user); // 
    } else {
      setMessage('');
    }
  };

  // 
  const handleLogout = () => {
    setMessage('');
    setCurrentUser(null); // 
  };

  // 
  const handleUpdate = (newName, newPassword) => {
    if (currentUser) {
      console.log(currentUser);
      // 
      const updatedUser = { ...currentUser, Name: newName, password: newPassword };
      
      //  users 
      const updatedUsers = users.map(user => 
        user.account === currentUser.account ? updatedUser : user
      );
    

      //  state  users 
      setUsers(updatedUsers);
      setCurrentUser(updatedUser); // 
      setMessage(``);
      console.log(updatedUser);
    }
  };

  // 
  const handleDelete = () => {
    if (currentUser) {
      // 
      const updatedUsers = users.filter(user => user.account !== currentUser.account);

      // 
      setUsers(updatedUsers); //  users 
      setCurrentUser(null); // 
      setMessage('');
    }
  };

  return (
    
    <div className="App">
      {/*  users  */}
      <h3> Users </h3>
      <table border="1">
        <thead>
          <tr>
            <th>Name</th>
            <th>Account</th>
            <th>Password</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user, index) => (
            <tr key={index}>
              <td>{user.Name}</td>
              <td>{user.account}</td>
              <td>{user.password}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <h2></h2>
      {!currentUser ? (
        // 
        <form onSubmit={handleLogin}>
          <div>
            <label htmlFor="account"></label>
            <input
              type="text"
              id="account"
              value={account}
              onChange={(e) => setAccount(e.target.value)}
              required
            />
          </div>
          <div>
            <label htmlFor="password"></label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <button type="submit"></button>
        </form>
      ) : (
        // 
        <div>
          <h3>{currentUser.Name}</h3>
          <button onClick={handleLogout}></button>
          <div>
            <h4></h4>
            <label htmlFor="newName"></label>
            <input
              type="text"
              id="newName"
              placeholder=""
              onChange={(e) => setAccount(e.target.value)} // 
            />
            <label htmlFor="newPassword"></label>
            <input
              type="password"
              id="newPassword"
              placeholder=""
              onChange={(e) => setPassword(e.target.value)} // 
            />
            <button onClick={() => handleUpdate(account, password)}></button>
          </div>
          <button onClick={handleDelete}></button>
        </div>
      )}
      {message && <p>{message}</p>}
    </div>
  );
}

export default App;
